<template>
    <div class="wrapper">
      <div class="address-wrapper">
        <form action="">
          <Draggable v-model="addresses" class="list-group" item-key="id" :options="{ handle: '.drag-handle' }">
            <div v-for="address in addresses" :key="address.id" class="text-container">
              <span class="drag-handle">☰</span> <!-- Drag handle icon -->
              <input
                type="text"
                :id="address.id"
                class="text-box"
                :placeholder="address.placeholder"
                v-model="address.value"
              />
            </div>
          </Draggable>
        </form>
      </div>
    </div>
  </template>
  
  
<script>
import { ref, defineComponent } from 'vue';
import { VueDraggableNext } from 'vue-draggable-next';

export default defineComponent({
  name: "StepOne",
  components: {
    Draggable: VueDraggableNext,
  },
  setup() {
    const addresses = ref([
      { id: 'pickup', placeholder: 'Add Pick up address', value: '' },
      { id: 'drop-off', placeholder: 'Add Drop off address', value: '' },
    ]);

    return {
      addresses,
    };
  },
});
</script>

<style scoped lang="scss">
@use '@/css/scss/_variables.scss' as *;

.wrapper {
  display: flex !important;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  padding: 5%;
  height: 100%;
  width: 100%;
}
.address-wrapper {
  height: 30%;
  width: 100%;
  border: 1px solid $txt-success;
  border-radius: 5px;
}
.text-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.drag-handle {
  cursor: grab;
  margin-right: 10px;
}
.text-box {
  width: 100%;
  padding: 10px;
  border: 1px solid $txt-success;
  border-radius: 5px;
}
</style>